
.composition{
  margin-bottom:50px;
}


.composition__title{
  font-size: 36px;
  line-height: 40px;
  margin: 0px;
  margin-top: 25px;
  margin-bottom: 40px;
}

.composition__item-description{
  width: 200px;
  font-family: $font-second;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  padding-left: 80px;
  margin: 0px;
  margin-bottom: 25px;

}

.composition__list{
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.composition__item--benefit{
  background-image: url(../img/composition-img1.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.composition__item--simply{
  background-image: url(../img/composition-img2.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.composition__item--functionality{
  background-image: url(../img/composition-img3.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.composition__item--result{
  background-image: url(../img/composition-img4.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
}
@media (min-width: $tablet-width){
  .composition__title{
    font-style: normal;  /*ТАК БЫТЬ НЕ ДОЛЖНО!!*/
    font-weight: normal;
    font-size: 60px;
    line-height: 60px;
    margin-top: 60px;
    margin-bottom: 70px;
  }

  .composition__list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .composition__item{
    position: relative;
    width: 324px;
    padding: 0px;
    padding-top: 20px;
    padding-bottom: 27px;
  }


  .composition__item-description{
    font-size: 16px;
    line-height: 24px;
    width: 250px;
    padding: 0px;
    padding-top: 120px;
  }

  .composition__item--benefit{
    position: relative;
    background-image: none;
  }

  .composition__item--benefit::before{
  position: absolute;
  top: 10px;
  left: 5px;
  content: "";

  width: 80px;
  height: 80px;

  background-image: url(../img/icon-benefit-tablet.svg);
  background-repeat: no-repeat;
  }

  .composition__item--1{
    position: relative;
  }

  .composition__item--1::after{
    position: absolute;
    top: 10px;
    right: 80px;
    content: "1";

    width: 112px;
    height: 280px;

    font-size: 280px;
    line-height: 280px;

    color: #F2F2F2;

    z-index: -1;

  }






  .composition__item--simply{
    background-image: url(../img/composition-2-tablet.png);
    background-repeat: no-repeat;
    background-position: 0 0;
  }

  .composition__item--functionality{
    background-image: url(../img/composition-3-tablet.png);
    background-repeat: no-repeat;
    background-position: 0 0;
  }

  .composition__item--result{
    background-image: url(../img/composition-4-tablet.png);
    background-repeat: no-repeat;
    background-position: 0 0;
  }

}

@media (min-width:$desktop-width){
  .composition__item{
    width:245px;
    padding-right: 50px;
  }


}
