.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 24px;
  height: 18px;
  background-color: transparent;
  cursor: pointer;
  border: none; }
  .main-nav__toggle:hover, .main-nav__toggle:active {
    background-color: transparent; }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000; }

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#000000, 30%);
  box-shadow: 0 8px 0 0 fade(#000000, 30%), 0 16px 0 0 fade(#000000, 30%); }

.main-nav--opened .main-nav__toggle {
  top: 0;
  left: 250px; }

.main-nav--opened {
  position: absolute;
  width: 18px;
  height: 18px;
  background-color: transparent;
  border: 0;
  cursor: pointer; }

.main-nav--opened::before,
.main-nav--opened::after {
  content: "";
  position: absolute;
  top: 75px;
  left: 5px;
  width: 18px;
  height: 18px;
  background-image: url(../img/close.svg);
  background-repeat: no-repeat;
  background-position: 0 0; }

