.gift {
  position: relative;
  background-image: url(../img/gift-photo-mobile-1.png);
  background-repeat: no-repeat;
  background-size: 280px auto;
  background-position: 0px 0px;
  margin-top: 40px;
  margin-bottom: 36px;
}

.gift::before {
  content: "";
  position: absolute;
  top: 58px;
  left: 101px;
  width: 78px;
  height: 80px;
  background-image: url("../img/icon_gift.svg");
  background-size: cover;
}

.gift__description {
  font-family: $font-second;
  padding-top: 186px;
  padding-bottom: 43px;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  color: #FFFFFF;
}

@media (min-width:$tablet-width) {
  .gift {
    background-image: url(../img/gift-photo-tablet-2.png);
    background-repeat: no-repeat;
    background-size: 708px auto;
    background-position: 0px 0px;
  }

  .gift__description {
    padding-top: 71px;
    padding-left: 423px;
    padding-bottom: 69px;
  }
}

@media (min-width:$desktop-width) {
  .gift {
    width: 245px;
    margin-left: 80px;
    background-image: url(../img/gift-photo-desktop-3.png);
    background-repeat: no-repeat;
    background-size: 245px auto;
    background-position: 0px 0px;
  }

  .gift__description {
    padding-top: 185px;
    padding-left: 10px;
    padding-bottom: 42px;
  }
}
